.OverviewCard {
  width: inherit;
  height: 400px;

  background-image: url('../../../../assets/Content/FirstCard/FirstCard.jpg');
  background-size: 120%;
  background-position-y: -290px;
  background-attachment: scroll;

  padding: 120px;

  color: rgb(255, 255, 255);
  border: 1px rgb(187, 65, 65);

  font-size: 40px;
}

.OverviewCard p {
  line-height: 50%;
}

.DefaultCard {
  width: inherit;
  height: 650px;
  background-color: #ff6a00;
  padding: 20px;
  color: white;
}

.OddCard {
  width: inherit;
  background-color: #2d2d2d;
  padding: 20px 0px 20px 0px;
  color: white;
}


